// components/ShopClient.tsx
'use client';
import React, { useMemo, useState } from 'react';
import { Product } from '../lib/products';
import ProductCard from './ProductCard';
import ControlsClient from './ControlsClient';
import { applyFilters, Opts, PageResult } from '../lib/util';

type Props = {
  allProducts: Product[];
  initialOpts: Opts;
  initialPageResult: PageResult;
};

export default function ShopClient({ allProducts, initialOpts, initialPageResult }: Props) {
  // keep opts in state; initial values must match SSR opts
  const [opts, setOpts] = useState<Opts>(initialOpts);

  const pageResult = useMemo(() => applyFilters(allProducts, opts), [allProducts, opts]);

  function onControlsChange(partial: Partial<Opts>) {
    setOpts((s) => ({ ...s, ...partial }));
  }

  return (
    <div className="shop-wrapper container-grid">
      <aside className="filters-area">
        <ControlsClient initial={initialOpts} onChange={onControlsChange} />
      </aside>

      <section className="product-area">
        <div id="product-list" className="product-grid">
          {/* render the pageResult items */}
          {pageResult.items.map((p) => (
            <ProductCard key={p.id} p={p} />
          ))}
        </div>

        <div id="pagination" className="pagination-wrap">
          <Pagination
            page={pageResult.page}
            totalPages={pageResult.totalPages}
            onPage={(page) => setOpts((s) => ({ ...s, page }))}
          />
        </div>
      </section>
    </div>
  );
}

function Pagination({ page, totalPages, onPage }: { page: number; totalPages: number; onPage: (p: number) => void }) {
  if (totalPages <= 1) return null;
  const buttons = [];
  for (let i = 1; i <= totalPages; i++) {
    buttons.push(
      <button
        key={i}
        className={`page-btn${i === page ? ' active' : ''}`}
        aria-current={i === page ? 'page' : undefined}
        onClick={() => onPage(i)}
      >
        {i}
      </button>
    );
  }
  return <nav className="pagination">{buttons}</nav>;
}