// components/ControlsClient.tsx
'use client';
import React, { useState } from 'react';
import { Opts, SortOption } from '../lib/util';

type Props = {
  initial: Opts;
  onChange: (opts: Partial<Opts>) => void;
};

export default function ControlsClient({ initial, onChange }: Props) {
  const [min, setMin] = useState(String(initial.minPrice));
  const [max, setMax] = useState(String(initial.maxPrice));
  const [sort, setSort] = useState<SortOption>(initial.sort);
  const [perPage, setPerPage] = useState(String(initial.perPage));

  function apply() {
    onChange({
      minPrice: Number(min || 0),
      maxPrice: Number(max || 9999999),
      sort,
      perPage: Number(perPage || 12),
      page: 1,
    });
  }

  return (
    <form id="filter-form" className="filters" onSubmit={(e) => e.preventDefault()}>
      <label className="label-inline">
        价格区间：
        <div className="range-inputs">
          <input id="price-min" name="min" type="number" min={0} value={min} onChange={(e) => setMin(e.target.value)} />
          <span className="sep">—</span>
          <input id="price-max" name="max" type="number" min={0} value={max} onChange={(e) => setMax(e.target.value)} />
        </div>
      </label>

      <label className="label-block">
        排序：
        <select id="sort-select" value={sort} onChange={(e) => setSort(e.target.value as SortOption)}>
          <option value="default">默认</option>
          <option value="price-asc">价格从低到高</option>
          <option value="price-desc">价格从高到低</option>
        </select>
      </label>

      <label className="label-block">
        每页：
        <select id="perpage-select" value={perPage} onChange={(e) => setPerPage(e.target.value)}>
          <option value="8">8</option>
          <option value="12">12</option>
          <option value="24">24</option>
        </select>
      </label>

      <div className="controls-actions">
        <button id="apply-btn" type="button" className="btn-primary" onClick={apply}>应用</button>
      </div>
    </form>
  );
}